const sliders = (slides, dir, prev, next) => {
    let slideIndex = 1, //ÑÐµÐºÑÑÐ¸Ð¹ ÑÐ»Ð°Ð¹Ð´, ÐºÐ¾ÑÐ¾ÑÑÐ¹ Ð¾ÑÐ¾Ð±ÑÐ°Ð¶Ð°ÐµÑÑÑ Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ
        paused = false;
    const items = document.querySelectorAll(slides);
         

    function showSlides(n) {
        if (n > items.length) {
            slideIndex =1;
        }

        if (n < 1) {
            slideIndex = items.length;
        }

        items.forEach(item => {
            item.classList.add('animated');
            item.style.display = 'none';
        });

        items[slideIndex - 1].style.display = 'block';
    }

    showSlides(slideIndex);

    function plusSledes(n) {
        showSlides(slideIndex += n);
    }

    try {
       const prevBtn = document.querySelector(prev),
            nextBtn = document.querySelector(next);

        prevBtn.addEventListener('click', () => {
            plusSledes(-1);
            items[slideIndex - 1].classList.remove('slideInLeft');
            items[slideIndex - 1].classList.add('slideInRight');
        });

        nextBtn.addEventListener('click', () => {
            plusSledes(1);
            items[slideIndex - 1].classList.remove('slideInRight');
            items[slideIndex - 1].classList.add('slideInLeft');
        });


    } catch(e){}

    function activateAnimation() {
        if (dir === 'vertical'){
            paused = setInterval(function(){
                plusSledes(1);
                items[slideIndex - 1].classList.add('slideInDown');
            }, 3000);
        } else {
            paused = setInterval(function(){
                plusSledes(1);
                items[slideIndex - 1].classList.remove('slideInRight');
                items[slideIndex - 1].classList.add('slideInLeft');
            }, 3000);
        }
    }

    activateAnimation();

    items[0].parentNode.addEventListener('mouseenter', () => {
        clearInterval(paused);
    }); //Ð²Ð·ÑÐ»Ð¸ ÑÐ¾Ð´Ð¸ÑÐµÐ»Ñ ÑÐ»Ð°Ð¹Ð´ÐµÑÐ° Ð¸ Ð½Ð° Ð½ÐµÐ³Ð¾ Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸Ðº ÑÐ¾Ð±ÑÑÐ¸Ñ 

    items[0].parentNode.addEventListener('mouseleave', () => {
        activateAnimation();
    }); 
    


};

export default sliders;

